<template>
	<view>
		<view class="page-section swiper">
			<view style="position: relative;">
				<view class="top_select" @click="open_select_compay()">
					<van-icon name="logistics" />
					<view class="com_name">
						{{compay_name.length >5?compay_name.slice(0, 5)+'...':compay_name}}
					</view>
					<van-icon name="arrow-down" v-show="!select_compay" />
					<van-icon name="arrow-up" v-show="select_compay" />
				</view>
				<swiper :indicator-dots="indicatorDots" :indicator-color="indicatorColor"
					:indicator-active-color="indicatorActiveColor" :autoplay="autoplay" :interval="interval"
					:duration="duration" style="height: 500rpx;">
					<swiper-item v-for="(banner,indexbn) in bannerdata" :key="indexbn">
						<image :src="banner.value" style="width: 750rpx;height: 500rpx;"></image>
					</swiper-item>

				</swiper>
			</view>
			<view class="main_i">
				<view class="title_line">我的工资单</view>
				<view class="gongzi" @click="To_gongzhi()">
					<image src="/static/image/index_top.png" style="width:100%; height: 400rpx;" mode='widthFix'>
					</image>
				</view>
				<view class="title_line">车辆维保管理</view>
				<view class="index_lanmu">
					<view class="index_lm_zi" @click="on_func_notice()">
						<view class="index_lm_zim index_lm_zim1">
							<view class="index_lm_tit1">报修工单</view>
						</view>
					</view>
					<view class="index_lm_zi" @click="on_func_notice()">
						<view class="index_lm_zim index_lm_zim2">
							<view class="index_lm_tit1 index_lm_tit2">维修记录</view>
						</view>
					</view>
					<view class="index_lm_zi" @click="on_func_notice()">
						<view class="index_lm_zim index_lm_zim3">
							<view class="index_lm_tit1 index_lm_tit3">车辆保养</view>
						</view>
					</view>
					<view class="index_lm_zi" @click="on_func_notice()">
						<view class="index_lm_zim index_lm_zim4">
							<view class="index_lm_tit1 index_lm_tit4">车辆处置</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		<van-popup :show="show_team">
			<view class="pop_are">
				<view class="tit">绑定车队</view>
				<view class="input_are">
					<!-- focus -->
					<input class="uni-input " v-model="team" />
				</view>
				<view class="ft_btnline">

					<van-button type="info" size="normal" block color="#3E3EF7" @click="set_team_ok()">确认</van-button>
				</view>
			</view>


		</van-popup>
		<!-- <van-action-sheet :show="select_compay" :actions="compay_list" :round="false" @cancel="onClose" @select="onSelect" 
			cancel-text="取消">
		</van-action-sheet> -->
		<van-popup :show="select_compay" position="bottom" custom-style="height:150px;" @close="onClose" round>
			<view class="select_are">
				<view class="select_btn">
					<view class="select_l" @click="onClose">取消</view>
					<view class="select_r" @click="onSelect">确认</view>
				</view>
				<view style="height: 105px; overflow-y: scroll;">
					<view class="select_item" @click="get_item(item,indexd)" v-for="(item,indexd) in compay_list"
						:key="indexd">{{item.companyName}}
						<view style="width: 50px; display: block;">
							<van-icon name="success" v-show="getitem==indexd?true:false" color="green" />
						</view>

					</view>
				</view>
			</view>
		</van-popup>
		<van-toast id="van-toast"></van-toast>
		<view class="nav_bar" v-show="!select_compay">
			<view class="nav_zi">
				<view>
					<image :src="index_ico_h" style="width:24px; height: 24px;" mode='widthFix'>
					</image>
				</view>
				<view class="nav_hover">首页</view>
			</view>
			<view class="nav_zi" @click="jump(2)">
				<view>
					<image :src="user_ico" style="width:24px; height: 24px;" mode='widthFix'>
					</image>
				</view>
				<view>我的</view>
			</view>
		</view>
	</view>
</template>

<script>
	import Toast from '@/wxcomponents/vant/dist/toast/toast';
	export default {
		data() {
			return {
				getitem: 0,
				indicatorDots: true,
				autoplay: true,
				interval: 3000,
				duration: 500,
				bannerdata: [],
				indicatorColor: "#292b40",
				indicatorActiveColor: "#ffffff",
				show_team: false,
				team: '',
				get_data: '',
				compay_name: '正在获取中正在获取中',
				select_compay: false,
				compay_list: [],
				if_login_check: false,
				compay_id: '',
				index_ico: require("@/static/image/index.png"),
				index_ico_h: require("@/static/image/index_hover.png"),
				user_ico: require("@/static/image/usercenter.png"),
				user_ico_h: require("@/static/image/usercenter_hover.png")
			}
		},
		methods: {
			on_func_notice() {
				Toast.loading({
					duration: 0,
					message: '开发中...',
					forbidClick: true,
				});
				setTimeout(function() {
					Toast.clear()

				}, 1000)
			},
			jump(type) {
				if (type == 1) {
					uni.navigateTo({
						url: '/pages/index/index',
					});
				} else {
					uni.navigateTo({
						url: '/pages/usercenter/usercenter',
					});
				}
			},
			open_select_compay() {
				this.login_wx(2)
			},
			onClose() {
				this.select_compay = false
			},
			onSelect(event) {
				//console.log(event.detail)
				this.select_compay = false
				this.compay_name = this.compay_list[this.getitem].companyName
				this.compay_id = this.compay_list[this.getitem].companyId
				uni.setStorageSync('compay_name', this.compay_name)

				this.set_team()

			},
			get_item(data, index) {
				this.getitem = index
				this.get_data = data

			},
			To_gongzhi() {
				var ifLogin = uni.getStorageSync("if_login_check")
				if (ifLogin == '') {
					uni.navigateTo({
						url: '/pages/login/login_type/login_type',
					});
				} else {
					uni.navigateTo({
						url: '/pages/gongzi/gongzi',
					});
				}
			},
			async set_team_ok() {
				//作废
				if (this.team == '') {
					Toast.fail('请输入车队代号');
				} else {
					Toast.loading({
						duration: 0,
						message: '加载中...',
						forbidClick: true,
					});
					const res = await this.$myRuquest({
						url: '/api/getnewslist',
						method: 'post',
						data: {
							team: this.team
						}
					})
					if (res.code == 200) {
						Toast.clear();
						Toast.success('设置成功');
					}
				}
			},
			async get_team_info() {
				const res = await this.$myRuquest({
					url: '/api/app/yTLittleProgram/vehicleFleetList',
					header: {
						Authorization: "Bearer " + uni.getStorageSync('ifLogin'),
						"content-type": 'application/json'
					}
				})
				Toast.clear();
				//判断获取是否绑定了车队
				this.select_compay = true
				this.compay_list = res.data

				this.compay_list.forEach((item, index) => {
					if (item.companyName == this.compay_name) {
						this.getitem = index
					}
				})

				//this.compay_name
				// if (res.code == 200) {
				// 	this.select_compay = false
				// } else {

				// }
			},

			async set_team() {

				var _this = this

				const res = await this.$myRuquest({
					url: '/api/app/yTLittleProgram/changeVehicleFleet',
					method: "POST",
					data: {
						companyId: _this.compay_id
					},
					header: {
						Authorization: "Bearer " + uni.getStorageSync('ifLogin'),
						"content-type": 'application/json'
					}
				})
				if (res.code == 200) {
					this.select_compay = false
				} else {
					wx.showToast({
						title: '设置出错',
						icon: 'fail',
						duration: 5000
					})
					this.select_compay = false
				}
			},
			async get_banner() {

				var _this = this

				const res = await this.$myRuquest({
					url: '/api/app/yTLittleProgram/pictureList?iType=0'

				})
				if (res.code == 200) {

					_this.bannerdata = res.data
				} else {
					_this.bannerdata = []
				}
			},
			get_wx_info() {
				var _this = this
				wx.getUserInfo({
					success(res) {
						//_this.userInfo=res.userInfo
						uni.setStorageSync("userinfo", JSON.stringify(res.userInfo))
					}
				})
			},
			login_wx(type) {
				var _this = this
				wx.login({
					success(res) {
						if (res.code) {

							wx.request({
								url: "https://333gps.net:9010/api/app/WeChat/GetOpenId?js_code=" + res
									.code,
								method: "POST",
								success(res) {
									uni.setStorageSync('ifLogin', res.data.data.token)
									_this.check_user_ifregest(type)
									// setTimeout(function() {
									// 	uni.switchTab({
									// 		url: '/pages/index/index',
									// 	})
									// }, 2000)
								}
							})
						} else {
							wx.showToast({
								title: '出现错误',
								icon: 'fail',
								duration: 5000
							})
							console.log('出现错误！' + res.errMsg)
						}
					}
				})
			},

			async check_user_ifregest(type) {
				var _this = this
				const res = await this.$myRuquest({
					url: '/api/app/yTLittleProgram/isBind',
					method: "get",
					header: {
						Authorization: "Bearer " + uni.getStorageSync('ifLogin'),
						"content-type": 'application/json'
					}
				})
				//判断获取是否绑定了车队
				if (type == 1) {
					if (res.data.isLogin == 1) {
						uni.setStorageSync('if_login_check', true)
						if (res.data.isBind == 0) {
							_this.if_login_check = false
						} else {
							_this.if_login_check = true
							this.compay_name = res.data.userData.companyName
							uni.setStorageSync("compay_name", res.data.userData.companyName)
							uni.setStorageSync('compay_id', res.data.userData.companyId)
						}
					}

				} else {
					if (res.data.isLogin == 1) {
						if (res.data.isBind == 0) {
							_this.if_login_check = false
							Toast.fail({
								duration: 0,
								message: '未登录',
								forbidClick: true,
							});
							//uni.removeStorageSync("ifLogin")
							//uni.removeStorageSync("userinfo")
							uni.removeStorageSync("compay_name")
							uni.removeStorageSync("compay_id")
							setTimeout(function() {
								Toast.clear()
								uni.navigateTo({
									url: '/pages/login/login_type/login_type',
								});
							}, 2500)
						} else {
							_this.get_team_info()
							// if (this.compay_list.length == 1) {
							// 	//this.select_compay=true
							// } else {
							// 	_this.get_team_info()

							// }

						}
					} else {
						Toast.fail({
							duration: 0,
							message: '未登录',
							forbidClick: true,
						});
						//uni.removeStorageSync("ifLogin")
						//uni.removeStorageSync("userinfo")
						uni.removeStorageSync("compay_name")
						uni.removeStorageSync("compay_id")
						setTimeout(function() {
							uni.navigateTo({
								url: '/pages/login/login_type/login_type',
							});
						}, 2500)
					}

				}

			},
			async check_user_ifregest2(type) {
				var _this = this
				const res = await this.$myRuquest({
					url: '/api/app/yTLittleProgram/weChatLogin',
					method: "POST",
					header: {
						Authorization: "Bearer " + uni.getStorageSync('ifLogin'),
						"content-type": 'application/json'
					}
				})
				//判断获取是否绑定了车队
				if (type == 1) {
					if (res.data.isLogin == 1) {
						uni.setStorageSync('if_login_check', true)
						if (res.data.isBind == 0) {
							_this.if_login_check = false
						} else {
							_this.if_login_check = true
							this.compay_name = res.data.userData.companyName
							uni.setStorageSync("compay_name", res.data.userData.companyName)
							uni.setStorageSync('compay_id', res.data.userData.companyId)

						}
					}

				} else {
					if (res.data.isLogin == 1) {
						if (res.data.isBind == 0) {
							_this.if_login_check = false
							Toast.fail({
								duration: 0,
								message: '未登录',
								forbidClick: true,
							});
							//uni.removeStorageSync("ifLogin")
							//uni.removeStorageSync("userinfo")
							uni.removeStorageSync("compay_name")
							uni.removeStorageSync("compay_id")
							setTimeout(function() {
								uni.navigateTo({
									url: '/pages/login/login_type/login_type',
								});
							}, 2500)

						} else {
							_this.get_team_info()
							// if (this.compay_list.length == 1) {
							// 	//this.select_compay=true
							// } else {
							// 	_this.get_team_info()

							// }

						}
					} else {
						Toast.fail({
							duration: 0,
							message: '未登录',
							forbidClick: true,
						});
						//uni.removeStorageSync("ifLogin")
						//uni.removeStorageSync("userinfo")
						uni.removeStorageSync("compay_name")
						uni.removeStorageSync("compay_id")
						setTimeout(function() {
							uni.navigateTo({
								url: '/pages/login/login_type/login_type',
							});
						}, 2500)
					}
				}
			},

		},
		onLoad() {
			this.get_banner()
		},
		onShow() {
			Toast.clear()
			this.select_compay = false
			var ifLogin = uni.getStorageSync("ifLogin")
			var compay_name = uni.getStorageSync("compay_name")

			this.login_wx(1)
			
			if (compay_name !== '') {
				this.compay_name = compay_name
			} else {
				this.compay_name = '正在获取中...'
			}
		}
	}
</script>
<style>
	/deep/ .uni-swiper-dots-horizontal {
		bottom: 80rpx;
	}

	.gongzi /deep/ uni-image>img {
		width: 100%;

	}

	/deep/ .van-action-sheet__item {
		background: #fff;

	}
</style>
<style scoped>
	.top_select {
		position: absolute;
		top: 48px;
		left: 10px;
		border-radius: 50px;
		height: 32px;
		width: 130px;
		background-color: rgba(0, 0, 0, 0.2);
		display: block;
		z-index: 99;
		display: flex;
		padding: 0px 15px;
		padding-right: 5px;
		color: #fff;
	}

	.com_name {
		line-height: 32px;
		padding: 0 5px;
		font-size: 13px;
	}

	page {
		background: #F7F8FC;
	}

	.gongzi {
		margin-bottom: 28rpx;
	}

	.page-section {
		position: relative;
	}

	.index_lanmu {
		height: auto;
		overflow: hidden;
		width: 100%;
		display: flex;
		flex-wrap: wrap
	}

	.index_lm_zi {
		width: 50%;
	}

	.index_lm_zim {
		border-radius: 16rpx;
		height: 144rpx;
	}

	.index_lm_zim1 {

		background: url("../../static/image/index_gl_01s.png") no-repeat right bottom #EBEBFF;
		background-size: 43%;
		margin: 0px 20rpx 20rpx 0;
		border: 1px solid #E4E4FF;
	}

	.index_lm_zim2 {

		background: url("../../static/image/index_gl_02s.png") no-repeat right bottom #FFEFEB;
		background-size: 43%;
		margin: 0px 0px 0px 20rpx;
		border: 1px solid #FFE8E4;
	}

	.index_lm_zim3 {
		background: url("../../static/image/index_gl_03s.png") no-repeat right bottom #FFF7EB;
		background-size: 43%;
		margin: 0px 20rpx 20rpx 0;
		border: 1px solid #FBEDDD;
	}

	.index_lm_zim4 {
		background: url("../../static/image/index_gl_04s.png") no-repeat right bottom #EDFBF9;
		background-size: 43%;
		margin: 0px 0px 0px 20rpx;
		border: 1px solid #DAF8F3;
	}

	.index_lm_tit1 {
		font-size: 28rpx;
		position: relative;
		color: #2E2E30;
		display: block;
		line-height: 144rpx;
		padding-left: 100rpx;
	}

	.index_lm_tit1::after {
		content: '';
		left: 8%;
		top: 27%;
		height: 35px;
		width: 35px;
		position: absolute;

		background: url("../../static/image/index_gl_01.png") no-repeat center;
		background-size: 100%;
	}

	.index_lm_tit2::after {
		content: '';
		left: 8%;
		top: 27%;
		height: 35px;
		width: 35px;
		position: absolute;

		background: url("../../static/image/index_gl_02.png") no-repeat center;
		background-size: 100%;
	}

	.index_lm_tit3::after {
		content: '';
		left: 8%;
		top: 27%;
		height: 35px;
		width: 35px;
		position: absolute;

		background: url("../../static/image/index_gl_03.png") no-repeat center;
		background-size: 100%;
	}

	.index_lm_tit4::after {
		content: '';
		left: 8%;
		top: 27%;
		height: 35px;
		width: 35px;
		position: absolute;

		background: url("../../static/image/index_gl_04.png") no-repeat center;
		background-size: 100%;
	}

	.banner {
		width: 750rpx;
		height: 482rpx;
		background-color: #0f0f27;

	}

	.banner .swiper {
		width: 750rpx;
		height: 482rpx !important;
	}

	.banner .swiper-item {
		width: 750rpx;
		height: 482rpx !important;
	}

	.banner .swiper-item image {
		display: block;
		width: 750rpx;
		height: 482rpx !important;
	}

	.main_ge {
		height: 30px;
		display: block;
		width: 100%;
		background: #3E3EF7;
	}

	.main_i {
		background: #F7F8FC;
		border-radius: 44rpx 44rpx 0 0;
		padding: 40rpx 40rpx;

		display: block;
		width: calc(100% - 80rpx);
		z-index: 90;
		position: absolute;
		left: 0;
		bottom: -730rpx;
	}

	.title_line {
		color: #2E2E30;
		font-size: 32rpx;
		padding-left: 20rpx;
		position: relative;
		margin-bottom: 28rpx;
	}

	.selectitem {
		position: relative;
	}

	.selectitem::after {
		content: ;
		right: 10px;
		top: 10px;
	}

	.title_line::after {
		content: '';
		position: absolute;
		left: 2px;
		top: 0;
		background: #3E3EF7;
		height: 20px;
		width: 2px;
		border-radius: 5px;
	}

	.pop_are {
		height: auto;
		padding-top: 10px;
		width: calc(100vw - 32px);
		margin: auto;
		background: #fff;
		border-radius: 8px;
	}

	/deep/ .van-popup {
		z-index: 99999;
		background: none;
	}

	.input_are {
		width: calc(100vw - 96px);
		margin: auto;
		padding: 16px;

		background: #F7F8FC;
		border-radius: 8px;
	}

	.tit {
		font-size: 16px;
		text-align: center;
		line-height: 30px;
		padding-bottom: 10px;
	}

	.ft_btnline {

		padding: 16px 16px
	}



	/* // */
	.select_are {
		background-color: #ffffff;
		width: 100vw;
		height: 150px;
		overflow: hidden;
	}

	.select_btn {
		display: block;
		height: 45px;
		background-color: #ffffff;

	}

	.select_l {
		float: left;
		width: 60px;
		font-size: 16px;
		text-align: center;
		line-height: 40px;
		color: #9C9FB7;
	}

	.select_r {
		float: right;
		width: 60px;
		font-size: 16px;
		text-align: center;
		line-height: 40px;
		color: #3E3EF7;
	}

	.select_item {
		display: flex;
		width: 100%;
		padding: 14px 0px;
		font-size: 16px;
		background-color: #fff;
		border: none;
		cursor: pointer;
		text-align: center;
		justify-content: center;
	}

	.ft_btnare {
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		background: #fff;
		padding: 10px 0;
	}

	.ft_btn {
		width: 90%;
		margin: auto;
		display: block;
	}
</style>
